import React from 'react';
import styled from 'styled-components';
import logo from '../assets/gbflogo.png'; // Certifique-se de que o logo está no caminho correto
import { useNavigate } from 'react-router-dom';

// Estilo para o contêiner principal
const HomeContainer = styled.div`
  font-family: 'Montserrat', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(180deg, #011E3B 0%, #343a40 100%);
  text-align: center;
`;

// Estilo para o título
const Title = styled.h1`
  font-size: 36px;
  font-weight: bold;
  color: #ffffff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
`;

// Estilo para os botões
const ButtonContainer = styled.div`
  display: flex;
  gap: 20px;
  margin-top: 30px;
`;

const Button = styled.button`
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  background: linear-gradient(90deg, #ddba33, #b5972d);
  padding: 15px 30px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    transform: scale(1.1);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  }
`;

const Home = () => {
  const navigate = useNavigate();

  const handleInstitutionalClick = () => navigate('/institucional');
  const handleAppClick = () => navigate('/app');

  return (
    <HomeContainer>
      <Title>Bem-vindo ao Global Force Bank</Title>
      <img src={logo} alt="Global Force Bank" width="300" />
      <ButtonContainer>
        <Button onClick={handleInstitutionalClick}>X Institucional</Button>
        <Button onClick={handleAppClick}>X Invest App x</Button>
      </ButtonContainer>
    </HomeContainer>
  );
};

export default Home;

