import React, { useState } from "react";
import styled from "styled-components";
import {
  Container,
  Typography,
  TextField,
  Button,
  Box,
  Grid,
  FormControlLabel,
  Checkbox,
  Snackbar,
  Alert,
} from "@mui/material";

// Estilização principal
const CadastroContainer = styled(Container)`
  font-family: "Montserrat", sans-serif;
  background: linear-gradient(180deg, #011e3b, #ffffff 100%);
  border-radius: 8px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  padding: 30px;
  margin-top: 50px;
  max-width: 600px;
`;

const Title = styled(Typography)`
  font-weight: bold;
  color: #011e3b;
  margin-bottom: 20px;
  text-align: center;
`;

const StyledButton = styled(Button)`
  background: linear-gradient(90deg, #ddba33, #b5972d);
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  transition: transform 0.3s ease, background 0.3s ease;

  &:hover {
    background: linear-gradient(90deg, #b5972d, #ddba33);
    transform: scale(1.05);
  }
`;

const CadastroInvestidor = () => {
  const [formData, setFormData] = useState({
    nome: "",
    email: "",
    cpf: "",
    celular: "",
    concorda: false,
  });
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");

  const handleInputChange = (e) => {
    const { name, value, checked, type } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!formData.concorda) {
      setSnackbarMessage("É necessário aceitar os termos para continuar.");
      setSnackbarOpen(true);
      return;
    }
    console.log("Dados do Investidor:", formData);
    setSnackbarMessage("Cadastro realizado com sucesso!");
    setSnackbarOpen(true);
    // Aqui, insira a lógica de envio para a API.
  };

  return (
    <CadastroContainer>
      <Title variant="h4">Abra sua Conta</Title>
      <Typography variant="body1" gutterBottom>
        Preencha o formulário abaixo para começar sua jornada de investimentos.
      </Typography>

      <form onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          {/* Nome completo */}
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Nome Completo"
              name="nome"
              value={formData.nome}
              onChange={handleInputChange}
              required
            />
          </Grid>

          {/* E-mail */}
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="E-mail"
              name="email"
              type="email"
              value={formData.email}
              onChange={handleInputChange}
              required
            />
          </Grid>

          {/* CPF */}
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="CPF"
              name="cpf"
              type="text"
              value={formData.cpf}
              onChange={handleInputChange}
              required
            />
          </Grid>

          {/* Celular */}
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Celular com DDD"
              name="celular"
              type="tel"
              value={formData.celular}
              onChange={handleInputChange}
              required
            />
          </Grid>

          {/* Termos e Condições */}
          <Grid item xs={12}>
            <FormControlLabel
              control={
                <Checkbox
                  checked={formData.concorda}
                  onChange={handleInputChange}
                  name="concorda"
                  color="primary"
                />
              }
              label="Concordo com os termos e condições"
            />
          </Grid>
        </Grid>

        <Box display="flex" justifyContent="center" mt={3}>
          <StyledButton type="submit">Cadastrar</StyledButton>
        </Box>
      </form>

      {/* Snackbar para mensagens */}
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={3000}
        onClose={() => setSnackbarOpen(false)}
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
      >
        <Alert onClose={() => setSnackbarOpen(false)} severity="info">
          {snackbarMessage}
        </Alert>
      </Snackbar>
    </CadastroContainer>
  );
};

export default CadastroInvestidor;

